.video-section {
  height: 600px;
  position: relative;
}

.video-section-photo {
  position: relative;
}

.video-section-video {
  position: absolute;
  right: 0;
  bottom: 0;
  cursor: pointer;
  box-shadow: 0 15px 35px rgb(0 0 0 / 30%);
}

.video-section-text {
  position: relative;
  width: 35rem;
  padding: 4rem 6rem;
}

.video-section-text button {
  padding-top: 2rem;
}
