.join-the-club {
  position: relative;
  text-align: center;
  width: 40%;
  margin: auto;
  padding: 9rem 0;
}

.join-the-club img {
  position: absolute;
  z-index: -2;
  width: 1440px;
  height: 1337px;
  margin-left: -725px;
  margin-top: -600px;
}

.join-the-club p {
  padding: 1.5rem 0;
}

.join-the-club form {
  display: flex;
}

.join-the-club-text-input {
  width: 600px;
  height: 40px;
  padding-left: 1rem;
  margin: auto;
  border: none;
  background-color: #f5f5f5;
  outline: none;
  cursor: text;
}

.join-the-club-submit {
  cursor: pointer;
  min-width: 40px;
  height: 40px;
  border: none;
  background-color: #5da8ef;
  color: white;
}
