.board-background {
  position: relative;
  width: 250px;
  height: 320px;
  background-color: #ececec;
  bottom: 0;
  margin-top: 10rem;
  margin-left: -8rem;
}

.board-image {
  position: absolute;
  top: -180px;
  left: 5%;
}

.board-detail {
  position: absolute;
  text-align: left;
  top: 20%;
  right: -55%;
}

.board-price {
  text-align: center;
  padding-top: 30px;
}
