:root {
  --swiper-theme-color: #555;
  --swiper-navigation-size: 20px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.shop-slider {
  padding: 7rem 0;
  width: 1440px;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -720px;
  margin-right: -720px;
}

.beach-bg {
  position: absolute;
  z-index: -10;
  margin-top: -420px;
}

.shop-slider h2 {
  text-align: center;
}

.shop-slider h5 {
  text-align: center;
}

.mySwiper {
}

.swiper-button-next {
  padding-top: 100px;
}

.swiper-button-prev {
  padding-top: 100px;
}

.show-all-button {
  text-align: center;
  padding-top: 50px;
}
