.vertical-text-on-top-relative {
  position: relative;
  width: 1300px;
}

.vertical-text-on-top {
  position: absolute;
  right: 0;
  margin-top: -50px;
  display: flex;
  flex-direction: column;
}

.vertical-text-on-top h5 {
  writing-mode: vertical-rl;
  color: #ccc;
}

.vertical-text-on-top-icons {
  display: flex;
  flex-direction: column;
  margin-top: 120px;
  padding-top: 200px;
  height: 300px;
  justify-content: space-between;
  cursor: pointer;
}

.vertical-text-on-top-icon-smaller {
  width: 15px;
}
