* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

button {
  text-transform: uppercase;
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.vertical-lines-bg {
  height: 100%;
  border-left: black 1px solid;
}

.blue-font {
  color: #5da8ef;
}

.gray-font {
  color: #bbb;
}

.black-font {
  color: black;
}

.small-bold-font {
  font-family: 'Spartan', sans-serif;
  font-size: 9px;
  letter-spacing: 4px;
  font-weight: 800;
  text-transform: uppercase;
}

.big-bold-font {
  font-family: 'Playfair Display', serif;
  font-size: 62px;
}

.semi-big-bold-font {
  font-family: 'Playfair Display', serif;
  font-size: 42px;
}

.gray-small-font {
  font-family: 'Poppins', sans-serif;
  color: #8f8f8f;
  font-size: 14px;
  font-weight: 300;
}

.gray-small-title-font {
  font-family: 'Poppins', sans-serif;
  color: #7a7a7a;
