}

.swiper-slide {
  text-align: center;
  font-size: 18px;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.shop-slider {
  padding: 7rem 0;
  width: 1440px;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -720px;
  margin-right: -720px;
}

.beach-bg {
  position: absolute;
  z-index: -10;
  margin-top: -420px;
}

.shop-slider h2 {
  text-align: center;
}

.shop-slider h5 {
  text-align: center;
}

.mySwiper {
}

.swiper-button-next {
  padding-top: 100px;
}

.swiper-button-prev {
  padding-top: 100px;
}

