nav {
  height: 120px;
  display: flex;
  align-items: center;
}

ul {
  display: flex;
  margin-left: 200px;
}

li {
  list-style: none;
  margin: 0 2rem;
}

.header-slider {
  position: relative;
  height: 600px;
}

.text-area {
  height: 600px;
  padding-top: 12rem;
  z-index: 50;
  position: absolute;
  width: 30rem;
  display: flex;
  flex-direction: column;
}

.text-area h2 {
  padding-top: 0.5rem;
  font-size: 3rem;
}

.text-area p {
  padding-top: 0.5rem;
}

.image-area {
  z-index: 10;
  position: absolute;
  right: 0;
  top: 0;
}

.image-area img {
  width: 1000px;
  height: 600px;
  box-shadow: 0 15px 35px rgb(0 0 0 / 30%);
}

.pages-number {
  margin-top: auto;
}

.pages {
  display: flex;
  align-items: center;
}

.pages span {
  margin-top: 5px;
  padding: 0 0.2rem;
  font-size: 13px;
}

.arrow {
  font-size: 1.3rem;
  cursor: pointer;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  -webkit-transform: scale(1);
          transform: scale(1);
}

