@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Spartan:wght@700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300&family=Spartan:wght@700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Poppins:wght@300&family=Spartan:wght@700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

button {
  text-transform: uppercase;
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.vertical-lines-bg {
  height: 100%;
  border-left: black 1px solid;
}

.blue-font {
  color: #5da8ef;
}

.gray-font {
  color: #bbb;
}

.black-font {
  color: black;
}

.small-bold-font {
  font-family: 'Spartan', sans-serif;
  font-size: 9px;
  letter-spacing: 4px;
  font-weight: 800;
  text-transform: uppercase;
}

.big-bold-font {
  font-family: 'Playfair Display', serif;
  font-size: 62px;
}

.semi-big-bold-font {
  font-family: 'Playfair Display', serif;
  font-size: 42px;
}

.gray-small-font {
  font-family: 'Poppins', sans-serif;
  color: #8f8f8f;
  font-size: 14px;
  font-weight: 300;
}

.gray-small-title-font {
  font-family: 'Poppins', sans-serif;
  color: #7a7a7a;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 300;
}

.regular-medium-font {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  font-size: 26px;
  color: #333;
}

.medium-pink-font {
  font-family: 'Poppins', sans-serif;
  font-size: 20px;
  font-weight: 300;
  color: #ce60a8;
}

.big-number-font {
  font-family: 'Spartan', sans-serif;
  font-size: 130px;
  font-weight: 700;
  position: absolute;
  z-index: -2;
  color: #f9f9f9;
}

.bold-medium-font {
  font-family: 'Playfair Display', serif;
  font-size: 32px;
}
